.form-container {
    margin: 100px auto;
    width:1000px;
    box-shadow: 0 5px 7px 0 rgba(0,0,0,0.2);
    position:relative;
    border-radius: 10px;
    height: 600px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.close-btn{
    position:absolute;
    top:2%;
    right:3%;
    font-size: 1.5rem;
    z-index:1;
    color:white;
    cursor: pointer;
}

.form-content-left{
    background: linear-gradient(to right, #a8ff78, #78ffd6);
    border-radius: 10px 0 0 10px;
    position: relative;
}

.form-img{
    width:60%;
    height: 60%;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%)
}

.form-content-right{
    border-radius: 0 10px 10px 0;
    position: relative;
    background: linear-gradient(
        90deg,
        rgba(40, 40, 40) 0%,
        rgba(17, 17, 17) 100%
    );
}

.form{
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form h1{
    font-size:1rem;
    text-align: start;
    width:80%;
    margin-bottom: 1rem;
    color:white;
}

.form-inputs {
    margin-bottom: 0.5rem;
}

.form-inputs p{
    font-size:0.8rem;
    margin-top:0.5rem;
    color:#f00e0e;
}

.form-label{
    display:inline-block;
    font-size: 0.8rem;
    margin-bottom: 6px;
    color:#fff;
}

.form-input{
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height:40px;
    width:300px;
    border:none;
}

.form-input::placeholder{
    color:#595959;
    font-size: 12px;
}

.form-input-btn{
    width:80px;
    height:50px;
    margin-top:10px;
    border-radius: 2px;
    background: linear-gradient(to right, #a8ff78, #78ffd6);
    outline: none;
    border: none;
    color:white;
    font-size:1rem;
}

.form-input-btn:hover{
    cursor:pointer; 
}

.form-input-login{
    font-size:0.8rem;
    margin-top:10px;
    color:#fff;
    width:80%;
    text-align:center;
}

.form-input-login a{
    text-decoration: none;
    color:#27cdff;
    font-weight: 600;
}